const settings = {
    originPosition: {x: 0, y: 0},
    mousePosition: [{}, {}, {}],
    isMouseActive: false,
    previousVelocity: 0,
    currentVelocity: 0,
    lineWidth: 5,
    brushType: 'pen',
    velocity: 0,
    previousEvent: false
};

export default settings;



